import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";

import { Observable, Subscription } from "rxjs";

import { LocalStorageService } from './localStorage.service';

function getBase64Image(img) {

  var dataURL = img

  return dataURL.replace(/^data:image\/(png|jpg);base64,/, "");
}

@Injectable()
export class ImageService {

  constructor(private http: HttpClient , protected localStorage: LocalStorageService) {}


  public uploadImage(image: File, src : String): Observable<any> {
    const formData = new FormData();

    formData.append('image', image);



    var imgData = getBase64Image(src);

    return this.localStorage.setItem("imgData", imgData);
  }
}
