import { element } from 'protractor';

import { Component, OnInit, Input, AfterViewInit } from '@angular/core';

import * as watermark from 'watermarkjs';

declare var require: any;

@Component({
  selector: 'app-watermark',
  templateUrl: './watermark.component.html',
  styleUrls: ['./watermark.component.css']
})
export class WatermarkComponent implements OnInit, AfterViewInit {
  constructor() { }
  private _name = '';
  private _index: Number;
  _image: string;
  @Input()
  set srcImg(name: string) {
    this._name = (name && name.trim()) || '<no name set>';
  }
  get srcImg(): string { return this._name; }

  @Input()
  set index(value: Number) {
    this._index = value;
  }
  get index(): Number { return this._index; }

  ngOnInit() {
    this._image = this.srcImg;
  }

  ngAfterViewInit(){
    try {
      watermark([this._image]).image(watermark.text.center('Marca D\'agua', '48px sans-serif', '#fff', 0.5))
      .then((img) => {
        img.classList.add('img-fluid');
        document.getElementById('img-watermark-' + this._index).appendChild(img);
      });
    } catch (error) {
      console.log(this._image || error);
    }
  }

}
